import React from 'react';
import classnames from 'classnames';

import './style.css';

const Button = ({ onClick, children, className }) => {
  const classNameInput = classnames('UIKit-Button', className);
  return (
    <button className={classNameInput} onClick={onClick}>{children}</button>
  )
};

export default Button


