import React from 'react';
import classnames from 'classnames';

import './style.css';

const TextArea = ({ onChange, value, id, className }) => {
  const classNameInput = classnames('UIKit-TextArea', className);
  return (
    <textarea className={classNameInput} type="text" id={id} onChange={onChange} value={value}/>
  )
};

export default TextArea


