import React, { Component } from 'react'
import _ from 'lodash'

import Input from 'uikit/Input'
import TextArea from 'uikit/TextArea'
import GroupInput from 'uikit/GroupInput'
import Grid from 'uikit/Grid'
import Card from 'uikit/Card'
import Button from 'uikit/Button'
import ObjectViewer from 'catalog/ObjectViewer'
import './style.css'

class GeneratorReleaseNotes extends Component {

  constructor(props) {
    super(props)

    this.state = {
      version: '1.25.6',
      title: 'Release Notes 25 september ',
      features: [{
        title: 'Feature 1',
        description: 'Feature Description 1'
      },{
        title: 'Feature Feature 2',
        description: 'Description 2'
      }],
      fixes: [{
        description: 'Fix Description 1'
      },{
        description: 'Fix Description 2'
      }],
    }
  }

  onChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value
    })
  }

  onFeatureChange = (event, index, type) => {
    const features = this.state.features
    features[index] = {
      ...features[index],
      [type]: event.target.value
    }

    this.setState({
      features,
    })
  }

  addFeature = () => {
    const features = this.state.features
    features.push({
      title: '',
      description: '',
    })

    this.setState({
      features
    })
  }

  onFixChange = (event, index, type) => {
    const fixes = this.state.fixes
    fixes[index] = {
      ...fixes[index],
      [type]: event.target.value
    }

    this.setState({
      fixes,
    })
  }

  addFix = () => {
    console.debug('newFix')
    const fixes = this.state.fixes
    fixes.push({
      description: '',
    })

    this.setState({
      fixes
    })
  }

  onDeleteItem = (type, index) => {
    const current = this.state[type]
    current.splice(index, 1)
    this.setState({
      [type]: current
    })
  }


  onGenerateKeys = () => {
    const { version, title, features, fixes} = this.state
    const basePath = `releasenotes.${version.replace(/\./g, '_')}`
    const result = {}
    result[`${basePath}.title`] = title
    _.map(features, (item, index) => {
      result[`${basePath}.features.${index}.title`] = item.title
      result[`${basePath}.features.${index}.description`] = item.description
    })

    _.map(fixes, (item, index) => {
      result[`${basePath}.fixes.${index}.description`] = item.description
    })

    this.setState({
      result
    })
  }

  render() {


    return (
      <div className="GeneratorReleaseNotes">
        <div className="GeneratorReleaseNotes__row item-box">
          <GroupInput label="Version" type="column">
            <Input id="version" onChange={this.onChange} value={this.state.version}/>
          </GroupInput>

          <GroupInput label="Title" type="column">
            <Input className="-big" id="title" onChange={this.onChange} value={this.state.title}/>
          </GroupInput>
        </div>

        <div className="GeneratorReleaseNotes__column item-box">
          <div className="title">
            Features <Button className="add-btn" onClick={this.addFeature}>ADD</Button>
          </div>
          <Grid>
            {
              _.map(this.state.features, (feature, index) => {
                return (
                  <Card
                    onDelete={() => this.onDeleteItem('features', index)}
                    key={index}
                  >
                    <GroupInput label="Title" type="column">
                      <Input onChange={(e) => this.onFeatureChange(e, index, 'title')} value={feature.title}/>
                    </GroupInput>
                    <GroupInput label="Description" type="column">
                      <TextArea onChange={(e) => this.onFeatureChange(e, index, 'description')} value={feature.description}/>
                    </GroupInput>
                  </Card>
                )
              })
            }
          </Grid>
        </div>

        <div className="GeneratorReleaseNotes__column item-box">
          <div className="title">
            Fixes <Button className="add-btn" onClick={this.addFix}>ADD</Button>
          </div>
          <Grid>
            {
              _.map(this.state.fixes, (fix, index) => {
                return (
                  <Card
                    onDelete={() => this.onDeleteItem('fixes', index)}
                    key={index}
                  >
                    <GroupInput label="Description" type="column">
                      <TextArea onChange={(e) => this.onFixChange(e, index, 'description')} value={fix.description}/>
                    </GroupInput>
                  </Card>
                )
              })
            }
          </Grid>
        </div>
        <Button className="generate-btn" onClick={this.onGenerateKeys}>Generate KEYS!</Button>

        <ObjectViewer isColor>
          {this.state.result}
        </ObjectViewer>

      </div>
    )
  }
}

export default GeneratorReleaseNotes


