import React from 'react';
import classnames from 'classnames';

import './style.css';

const Grid = ({ children, className }) => {
  const classNameGrid = classnames('UIKit-Grid', className);
  return (
    <div className={classNameGrid}>
      {
        children
      }
    </div>
  )
};

export default Grid


