import React from 'react';
import classnames from 'classnames';

import './style.css';

const Card = ({ children, type, onDelete, className }) => {
  const classNameCard = classnames('UIKit-Card', className, `-${type}`);
  return (
    <div className={classNameCard}>
      <span className="delete" onClick={onDelete}>
        x
      </span>
      {
        children
      }
    </div>
  )
};

Card.defaultProps = {
  type: 'row'
};

export default Card


