import React from 'react';
import classnames from 'classnames';

import './style.css';

const GroupInput = ({ children, label, className, type }) => {
  const classNameInput = classnames('UIKit-GroupInput', className, `-${type}`);

  return (
    <div className={classNameInput}>
      <div className="label">
        {label}
      </div>

      <div className="field">
        {children}
      </div>
    </div>
  )
};

GroupInput.defaultProps = {
  type: 'row'
}

export default GroupInput


