import React from 'react';
import _ from 'lodash'
import classnames from 'classnames';

import './style.css';

const ObjectViewer = ({ children, isColor, className }) => {
  const classNameObjectViewer = classnames('Catalog-ObjectViewer', className);

  const syntaxHighlight = (json) => {
    json = json.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;');
    return json.replace(/("(\\u[a-zA-Z0-9]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?)/g, (match) => {
      let cls = 'number';
      if (/^"/.test(match)) {
        if (/:$/.test(match)) {
          cls = 'key';
        } else {
          cls = 'string';
        }
      } else if (/true|false/.test(match)) {
        cls = 'boolean';
      } else if (/null/.test(match)) {
        cls = 'null';
      }
      return '<span class="' + cls + '">' + match + '</span>';
    });
  };

  const getJson = () => {
    return JSON.stringify(children, undefined, 2)
  };

  const renderJson = () => {
    return isColor
      ? (
        <pre dangerouslySetInnerHTML={{__html: syntaxHighlight(getJson())}} />
      )
      : (
        <pre>
          {getJson()}
        </pre>
      )
  };

  return !_.isEmpty(children) ? (
    <div className={classNameObjectViewer}>
      {renderJson()}
    </div>
  ) : null
};

export default ObjectViewer


