import React from 'react';
import classnames from 'classnames';

import './style.css';

const Input = ({ onChange, value, id, className }) => {
  const classNameInput = classnames('UIKit-Input', className);
  return (
    <input className={classNameInput} type="text" id={id} onChange={onChange} value={value} />
  )
};

export default Input


